          <p style="font-size:0.4rem;">{{index+1}}.{{t.title}}</p>
          <p class="r" v-for="(cg, i) in t.catalog"><input data-t="illnessLevel" type="radio" :data-score="cg.score" :name="t.title" :ref="t.title":value="cg.desc" @click="handleSelfCareClick(t.title,i,'illnessLevel')">{{cg.score}}分{{cg.desc}}</p>
        </div>
