import {patientEvaluate} from './maps';

export default {
  getpatientEvaluate: function(keys, value, option) {
    let obj = {};
    var reg = /^[\d]+/;
    obj.title = patientEvaluate.key(keys);
    obj.score = value.match(reg)[0];
    var length = obj.score.length + 2;
    obj.desc = value.slice(length, -1);
    return obj;
  }
}


